.shop_container {
  width: 100%;
  min-height: 900px;
  padding-top: 150px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background-image: url("../media/fth_sec/g_fourth_bg.jpg");
  background-attachment: fixed;
  background-size: cover;
}

.shop_cwd_title {
  color: #e9e3c7;
  font-size: 45px;
  text-transform: uppercase;
  padding: 30px;
}

.shop_prod_wrapper {
  width: 90%;
  min-height: 500px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 150px;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-item-align: center;
      -ms-grid-row-align: center;
      align-self: center;
}

.shp_prodbox {
  min-height: 150px;
  position: relative;
  width: 250px;
  min-width: 150px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: 50px;
  overflow-x: hidden;
}

.shpbox_atcload {
  pointer-events: none;
}

.shpprodbox_img {
  height: 345px;
  width: 100%;
  -o-object-fit: contain;
     object-fit: contain;
}

.shpprodbox_title {
  color: #e9e3c7;
  margin: 15px 5px 5px 5px;
  word-wrap: break-word;
  text-align: center;
  text-transform: uppercase;
}

.shpprodbox_price {
  color: #d9ce9d;
  font-size: 20px;
  margin-bottom: 10px;
}

.shpprodbox_quantity {
  min-width: 80%;
  min-height: 25px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none;
}

.sprdbx_quantity_btn,
.sprdbx_quantity_quantno {
  height: 30px;
  width: 30px;
  line-height: 30px;
  background-color: rgba(221, 90, 18, 0.4);
  text-align: center;
  cursor: pointer;
  font-size: 20px;
  color: #000;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.sprdbx_quantity_btn:hover,
.sprdbx_quantity_quantno:hover {
  -webkit-transition: 0.2s;
  transition: 0.2s;
  background-color: #dd5a12;
}
.sprdbx_quantity_btn:active,
.sprdbx_quantity_quantno:active {
  color: #fff;
  -webkit-box-shadow: 0 0 2px 0 #000;
          box-shadow: 0 0 2px 0 #000;
}

.sprdbx_quantity_quantno {
  cursor: default;
  width: 100%;
  font-size: 18px;
  border: none;
  background-color: rgba(221, 90, 18, 0.6);
  color: #fff;
}
.sprdbx_quantity_quantno:hover {
  background-color: rgba(221, 90, 18, 0.6);
}

.sprodimg_coldbrew {
  width: 130%;
}

.sprodimg_spice {
  width: 105%;
}

.shpprodbox_addtocart {
  min-width: 80%;
  height: 30px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 15px 5px 15px 5px;
  background-color: #dd5a12;
  text-transform: uppercase;
  font-size: 13px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  cursor: pointer;
  border-radius: 3px;
}
.shpprodbox_addtocart:hover {
  background-color: rgba(221, 90, 18, 0.9);
  -webkit-box-shadow: 0 0 2px 0 #000;
          box-shadow: 0 0 2px 0 #000;
  color: #d9ce9d;
}
.shpprodbox_addtocart:active {
  color: #000;
  background-color: #dd5a12;
}

@media screen and (max-width: 960px) {
  .shop_prod_wrapper {
    width: 100%;
  }

  .shp_prodbox {
    width: 215px;
  }

  .shpprodbox_img {
    min-height: 320px;
    width: 150px;
  }

  .sprodimg_coldbrew {
    width: 130%;
  }

  .sprodimg_spice {
    width: 115%;
  }
}
@media screen and (max-width: 730px) {
  .shp_prodbox {
    width: calc(100% / 3);
  }

  .shpprodbox_img {
    min-height: 240px;
    max-height: 200px;
  }

  .shpprodbox_title {
    font-size: 12px;
  }

  .shpprodbox_price {
    font-size: 15px;
  }

  .shpprodbox_addtocart {
    height: 25px;
  }
}
@media screen and (max-width: 540px) {
  .shp_prodbox {
    width: calc(100% / 2);
  }
}
@media screen and (max-width: 350px) {
  .shp_prodbox {
    width: 100%;
  }
}