.fif_fif_wrhalf_maintxt {
  color: #e9e3c7;
  font-size: 65px;
  font-weight: bold;
  text-transform: uppercase;
}

.fif_fif_wrhalf_subtxt {
  color: #e9e3c7;
  font-size: 15px;
  text-align: center;
  padding: 5px;
}

.fif_fif_wrhalf_getdelbtn {
  text-transform: uppercase;
