.footer_container {
  position: relative;
  width: 100%;
  min-height: 300px;
  background-image: url("../media/f_sec/g_dirty_bg.png");
  background-size: cover;
}

.footer_container_wrapper {
  width: 100%;
  min-height: auto;
  padding: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.foot_cwrp_f,
.foot_cwrp_s {
  min-height: 60px;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.fcwrp_f_wrapchangeloc {
  height: 30px;
  width: 200px;
  color: #e9e3c7;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 15px;
}
.fcwrp_f_wrapchangeloc :nth-child(1) {
  display: block;
  height: 25px;
  width: 25px;
  margin-right: 5px;
  background: url("../media/footer/marker.png");
  background-repeat: no-repeat;
  background-size: cover;
}
.fcwrp_f_wrapchangeloc :nth-child(2) {
  cursor: pointer;
  text-transform: uppercase;
}
.fcwrp_f_wrapchangeloc :nth-child(3) {
  display: block;
  height: 20px;
  width: 20px;
  margin-right: 5px;
  background: url("../media/footer/next.png");
  background-repeat: no-repeat;
  background-size: cover;
  cursor: pointer;
}
.fcwrp_f_wrapchangeloc:hover {
  -webkit-transition: 0.2s;
  transition: 0.2s;
  opacity: 0.8;
}

.fcwrp_f_wraploclist {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  max-height: 0;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  overflow: hidden;
}
.fcwrp_f_wraploclist ul {
  margin-top: 20px;
  -webkit-columns: 4;
     -moz-columns: 4;
          columns: 4;
}
.fcwrp_f_wraploclist ul li {
  text-transform: uppercase;
  list-style: none;
  margin-bottom: 15px;
  margin-right: 15px;
  font-size: 15px;
}
.fcwrp_f_wraploclist ul li a {
  text-decoration: none;
  color: #d9ce9d;
}
.fcwrp_f_wraploclist ul li a:hover {
  opacity: 0.9;
}

.fcwrp_f_wloclist_show {
  max-height: 600px;
}

@media screen and (max-width: 500px) {
  .fcwrp_f_wraploclist ul {
    -webkit-columns: 2;
       -moz-columns: 2;
            columns: 2;
  }
}
@media screen and (min-width: 500.5px) and (max-width: 760px) {
  .fcwrp_f_wraploclist ul {
    -webkit-columns: 3;
       -moz-columns: 3;
            columns: 3;
  }
}
.foot_cwrp_s {
  min-height: 150px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}

.fcwrp_s_sec {
  width: calc(100% / 3);
  min-height: 150px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  color: #e9e3c7;
  padding: 10px;
}
.fcwrp_s_sec ul {
  padding: 0;
}
.fcwrp_s_sec ul li {
  list-style: none;
  margin: 0;
  text-transform: uppercase;
  margin-top: 10px;
  font-size: 15px;
  cursor: pointer;
}
.fcwrp_s_sec ul li:hover {
  opacity: 0.9;
}
.fcwrp_s_sec a:hover {
  opacity: 0.9;
}

.fcwrp_ssec_wrapflinks,
.fcwrp_ssec_wrapsociallinks {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  width: 100%;
  height: auto;
}
.fcwrp_ssec_wrapflinks a,
.fcwrp_ssec_wrapsociallinks a {
  margin-left: 10px;
  text-decoration: none;
  color: inherit;
}

.fcwrp_ssec_wrapsociallinks {
  margin-top: 10px;
}
.fcwrp_ssec_wrapsociallinks a {
  width: 35px;
  height: 35px;
  display: block;
  margin-left: 10px;
}
.fcwrp_ssec_wrapsociallinks a:nth-child(1) {
  background: url("../media/social_icons/facebook_foot.png");
}
.fcwrp_ssec_wrapsociallinks a:nth-child(2) {
  background: url("../media/social_icons/instagram_foot.png");
}
.fcwrp_ssec_wrapsociallinks a:nth-child(3) {
  background: url("../media/social_icons/twitter_foot.png");
}
.fcwrp_ssec_wrapsociallinks a:nth-child(4) {
  background: url("../media/social_icons/youtube_foot.png");
}
.fcwrp_ssec_wrapsociallinks a:nth-child(1),
.fcwrp_ssec_wrapsociallinks a:nth-child(2),
.fcwrp_ssec_wrapsociallinks a:nth-child(3),
.fcwrp_ssec_wrapsociallinks a:nth-child(4) {
  background-size: 100%;
  background-repeat: no-repeat;
}

.fcwrp_ssec_socialtxt {
  min-width: 100%;
  height: auto;
  text-align: right;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  margin-top: 20px;
}
.fcwrp_ssec_socialtxt span {
  font-size: 35px;
}

.foot_cwrp_t {
  margin: 10px 0 10px 0;
  width: 100%;
  height: auto;
  text-align: center;
  color: #d9ce9d;
  font-size: 10px;
  text-transform: uppercase;
  font-weight: bold;
  padding: 0 10px 0 10px;
}

@media screen and (max-width: 640px) {
  .footer_container_wrapper {
    padding: 10px;
  }

  .foot_cwrp_s {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }

  .fcwrp_s_sec ul {
    padding: 5px;
  }

  .fcwrp_s_sec_f,
.fcwrp_s_sec_s {
    width: calc(100% / 2);
  }

  .fcwrp_s_sec_th {
    width: 100%;
  }

  .fcwrp_ssec_wrapflinks {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  .fcwrp_ssec_wrapflinks a {
    margin: 10px;
  }

  .fcwrp_ssec_wrapsociallinks {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }

  .fcwrp_ssec_socialtxt {
    text-align: center;
  }

  .foot_cwrp_t {
    margin: 10px 0 30px 0;
  }

  .fcwrp_f_wrapchangeloc {
    margin: 10px;
  }
}