.ninth_section_container {
  position: relative;
  width: 100%;
  height: 100vh;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.ninth_ninth_wrhalf {
  position: relative;
  height: 100%;
  width: 50%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.ninth_ninth_wrhalf:hover .nth_nth_wrapsocialtag {
  opacity: 0.9;
}

.ninth_ninth_wrhalf_f {
  background: url("../media/ninth_sec/nth_f_img.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 40%;
}

.ninth_ninth_wrhalf_s {
  background: url("../media/ninth_sec/nth_s_img.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 45%;
}

.nth_nth_overlaybg {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: #000;
  opacity: 0.6;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.nth_nth_overlaybg:hover {
  opacity: 0.3;
}

.nth_nth_wrapsocialtag {
  position: absolute;
  min-width: 50%;
  height: 25px;
  left: 20px;
  bottom: 20px;
  opacity: 0.7;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.nth_nth_wrapsocialtag a {
  color: red;
  font-size: 10px;
  text-transform: uppercase;
  margin-left: 10px;
  letter-spacing: 1px;
  cursor: pointer;
  color: #d9ce9d;
  text-decoration: none;
}

.nthnth_wrsocialicon_insta {
  background: url("../media/social_icons/instagram.png");
  background-size: 80%;
}

.nthnth_wrsocialicon_twitt {
  background: url("../media/social_icons/twitter.png");
}

.nthnth_wrsocialicon {
  display: block;
  height: 25px;
  width: 25px;
  opacity: 0.8;
  background-size: cover;
  background-repeat: no-repeat;
}

.nthnth_wrsocialicon_insta {
  background-size: 90%;
}

@media screen and (max-width: 960px) {
  .ninth_ninth_wrhalf {
    width: 100%;
  }

  .ninth_section_container {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }

  .ninth_ninth_wrhalf_f {
    background-position: 50% 50%;
  }

  .ninth_ninth_wrhalf_s {
    background-position: 50% 70%;
  }
}