.contact_container {
  position: relative;
  overflow-y: auto;
  overflow-x: hidden;
  width: 100%;
  min-height: 100vh;
  background-image: url("../media/contact/ct_bg.png");
  background-attachment: fixed;
  background-size: cover;
}

.contact_cont_wrapper {
  min-height: 900px;
  width: 100vw;
}

.ct_cont_wrapdata {
  position: relative;
  width: 70%;
  min-height: 500px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin-top: 100px;
  padding: 30px;
  overflow-x: hidden;
}

.ct_cwd_title {
  color: #e9e3c7;
  font-size: 45px;
  text-transform: uppercase;
}

.ct_cwd_subtitle {
  width: 70%;
  color: #d9ce9d;
  font-size: 14px;
}

.ct_cwd_wrapinpvalue {
  width: 95%;
  min-height: 400px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin-top: 50px;
}

.ctcwd_wrinp_txt {
  color: #e9e3c7;
  font-weight: normal;
  font-size: 12px;
  text-transform: uppercase;
  margin: 15px 0 5px 0;
}

.ct_wrpinp_span {
  position: relative;
  width: 100%;
  height: 35px;
  max-width: 100%;
}
.ct_wrpinp_span input {
  position: absolute;
  width: 100%;
  height: 100%;
  border: 0;
  left: 0;
  top: 0;
  padding: 0 5px 0 5px;
  font-size: 14px;
  background-color: transparent;
  color: #d9ce9d;
  border: 1px solid #dd5a12;
  font-family: inherit;
}
.ct_wrpinp_span input:focus {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: 1px solid #dd5a12;
}

.ct_wrpinp_span_txtarea {
  height: 50px;
  position: relative;
  width: 100%;
  height: 100px;
  border: 1px solid #dd5a12;
  max-width: 100%;
}
.ct_wrpinp_span_txtarea textarea {
  font-family: inherit;
  position: absolute;
  width: 100%;
  height: 100%;
  border: 0;
  left: 0;
  top: 0;
  padding: 5px;
  font-size: 14px;
  resize: none;
  background-color: transparent;
  color: #d9ce9d;
}
.ct_wrpinp_span_txtarea textarea:focus {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: 1px solid #dd5a12;
}

.contact_submitform_btn {
  width: 40%;
  min-width: 150px;
  height: 35px;
  background-color: #dd5a12;
  color: #000;
  text-transform: uppercase;
  border-radius: 2px;
  text-align: center;
  line-height: 35px;
  margin: 25px 0 15px 0;
  cursor: pointer;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  font-size: 13px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none;
}
.contact_submitform_btn:hover {
  color: #fff;
  background-color: rgba(221, 90, 18, 0.8);
  -webkit-box-shadow: 0 0 5px 0 #000;
          box-shadow: 0 0 5px 0 #000;
}
.contact_submitform_btn:active {
  color: #000;
  background-color: #dd5a12;
  -webkit-box-shadow: 0 0 0 0 #000;
          box-shadow: 0 0 0 0 #000;
}

.ct_smbttxt_qry {
  font-size: 13px;
  margin-top: 20px;
}

.ct_smbttxt_qry_success {
  color: #408b12;
}

.ct_smbttxt_qry_error {
  color: #e62d28;
}

@media screen and (max-width: 1240px) {
  .contact_container {
    background-position: 50% 50%;
  }

  .ct_cont_wrapdata {
    width: 90%;
  }
}
@media screen and (max-width: 860px) {
  .ct_cont_wrapdata {
    width: 100%;
  }

  .ct_cwd_wrapinpvalue {
    width: 100%;
  }
}
@media screen and (max-width: 560px) {
  .ct_cont_wrapdata {
    padding: 15px;
  }

  .ct_cwd_subtitle {
    width: 80%;
  }
}