.f_cont_wrapper {
  position: relative;
  width: 100%;
  height: 100vh;
}

.f_cont_wrap {
  position: absolute;
  width: 60%;
  min-height: 100%;
  right: 0;
  top: 0;
  z-index: 2;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
}
.f_cont_wrap canvas {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.f_right_section {
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.f_fsec_ul_bigtxt {
  font-weight: bold;
  width: 100%;
  color: #e9e3c7;
  text-transform: uppercase;
  margin: 10px;
  position: relative;
}

.f_ul_bigsc {
  color: #ffff;
  list-style: none;
}

.f_fsec_ul_bigtxt {
  font-size: 70px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none;
}

.fseculbtxt_jgmst {
  color: #dd5a12;
}

.f_fsec_ul_expmtxt {
  color: #dd5a12;
  margin: 10px;
}

.f_ul_bigsc {
  display: block;
}

.ful_bgsec_arr {
  position: relative;
  width: 20px;
  height: 45px;
  margin: 30px 0 0 10px;
  display: block;
  opacity: 0.8;
}
.ful_bgsec_arr span {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-animation: 3s flbgseca_arranim linear infinite;
          animation: 3s flbgseca_arranim linear infinite;
  left: 0;
  top: 0;
  background: url("../media/f_sec/down-arrow.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

@-webkit-keyframes flbgseca_arranim {
  0% {
    margin-top: 5px;
  }
  25% {
    margin-top: 10px;
  }
  50% {
    margin-top: 15px;
  }
  75% {
    margin-top: 10px;
  }
  100% {
    margin-top: 5px;
  }
}

@keyframes flbgseca_arranim {
  0% {
    margin-top: 5px;
  }
  25% {
    margin-top: 10px;
  }
  50% {
    margin-top: 15px;
  }
  75% {
    margin-top: 10px;
  }
  100% {
    margin-top: 5px;
  }
}
.fseculbtxt_tyk {
  margin-left: -800px;
}

.fseculbtxt_tyk_show {
  -webkit-animation: 0.7s fseculbtxt_tyk_anim ease-in forwards;
          animation: 0.7s fseculbtxt_tyk_anim ease-in forwards;
}

@-webkit-keyframes fseculbtxt_tyk_anim {
  from {
    margin-left: -800px;
  }
  to {
    margin-left: 0;
  }
}

@keyframes fseculbtxt_tyk_anim {
  from {
    margin-left: -800px;
  }
  to {
    margin-left: 0;
  }
}
.fseculbtxt_jgmst_show {
  -webkit-animation: 0.7s fseculbtxt_jgmst_showanim ease-in forwards;
          animation: 0.7s fseculbtxt_jgmst_showanim ease-in forwards;
}

.fseculbtxt_jgmst {
  opacity: 0;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

.fseculbtxt_tkagain {
  opacity: 0;
  -webkit-animation-delay: 3.8s;
          animation-delay: 3.8s;
}

.fseculbtxt_expmor {
  opacity: 0;
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}

.ful_bgsec_arr {
  opacity: 0;
  -webkit-animation-delay: 4.2s;
          animation-delay: 4.2s;
}

.th_wrhlf_f_txt {
  opacity: 0;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

.thrd_thrd_wrhalf_wrap {
  opacity: 0;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

@-webkit-keyframes fseculbtxt_jgmst_showanim {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fseculbtxt_jgmst_showanim {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.f_ul_smsc {
  display: none;
}

@media screen and (max-width: 1255px) {
  .f_fsec_ul_bigtxt {
    font-size: 55px;
  }
}
@media screen and (max-width: 1000px) {
  .f_cont_wrap {
    z-index: 1;
  }

  .f_right_section {
    z-index: 2;
  }
}
@media screen and (max-width: 840px) {
  .fifth_fifth_wrhalf_f {
    display: none;
  }

  .fifth_fifth_wrhalf_s {
    width: 100%;
  }
}
@media screen and (max-width: 600px) {
  .f_ul_bigsc {
    padding: 20px;
  }
}
@media screen and (min-width: 500.5px) and (max-width: 550px) {
  .f_fsec_ul_bigtxt {
    font-size: 50px;
  }
}
@media screen and (max-width: 500px) {
  .f_ul_bigsc {
    display: none;
  }

  .f_ul_smsc {
    display: block;
  }

  .ful_bgsec_arr {
    opacity: 1;
    height: 30px;
    margin-top: 20px;
  }

  .f_right_s_wrtxt .f_ul_smsc {
    padding: 20px;
  }
  .f_right_s_wrtxt .f_ul_smsc li {
    list-style: none;
    margin: 0;
  }
  .f_right_s_wrtxt .f_ul_smsc :nth-child(1) {
    font-size: 60px;
    color: #e9e3c7;
  }
  .f_right_s_wrtxt .f_ul_smsc :nth-child(2) {
    font-size: 20px;
    color: #dd5a12;
  }
  .f_right_s_wrtxt .f_ul_smsc :nth-child(3) {
    font-size: 30px;
    margin-top: 20px;
  }

  .f_cont_wrap {
    z-index: 1;
    margin-top: 120px;
  }

  .f_right_section {
    z-index: 2;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    padding-top: 100px;
  }
}