body {
  font-family: "Merriweather", serif;
  padding: 0;
  margin: 0;
  min-width: 300px;
  overflow-x: hidden;
  background-color: #000000;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.main_container {
  position: relative;
}

.wrap_home_container {
  position: relative;
  overflow-y: auto;
  overflow-x: hidden;
  width: 100%;
  height: 100vh;
}

.home_section {
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.m_section_sixthsec {
  height: 40%;
}

.home_section:nth-child(1) {
  background-image: url("../media/f_sec/g_dirty_bg.png");
  background-attachment: fixed;
  background-size: cover;
}

.home_section:nth-child(2) {
  background-image: url("../media/s_sec/s_sec_absgreen_bg.png");
  background-attachment: fixed;
  background-size: cover;
}

.home_section:nth-child(3) {
  background-image: url("../media/thrd_sec/th_manifest_bg.jpg");
  background-attachment: fixed;
  background-size: cover;
}

.home_section:nth-child(4) {
  background-image: url("../media/fth_sec/g_fourth_bg.jpg");
  background-attachment: fixed;
  background-size: cover;
}

.home_section:nth-child(5) {
  background-image: url("../media/fifth_sec/fifth_jag_bg.jpg");
  background-attachment: fixed;
  background-size: cover;
}

.home_section:nth-child(6) {
  background-image: url("../media/sixth_sec/bg_barrels_sx.jpg");
  background-attachment: fixed;
  background-size: cover;
}

.home_section:nth-child(7) {
  background-image: url("../media/seventh_sec/svth_bg.jpg");
  background-attachment: fixed;
  background-size: cover;
  background-position: 50% 50%;
}

.home_section:nth-child(8) .home_content {
  height: 100vh;
}
.home_section:nth-child(8) iframe {
  width: 100%;
  height: 100%;
}

.home_section:nth-child(10) {
  height: 20%;
  background-image: url("../media/ten_sec/ten_abgreen_bg.jpg");
  background-attachment: fixed;
  background-size: cover;
  background-position: 50% 50%;
}

@media screen and (max-width: 760px) {
  .home_section:nth-child(3) {
    background-position: 50%;
  }

  .home_section:nth-child(5) {
    background-position: 30%;
  }

  .home_section:nth-child(6) {
    background-position: 50%;
  }
}
@media screen and (max-width: 560px) {
  .wrap_home_container {
    min-height: 100%;
  }

  .home_section {
    min-height: 300px;
  }
}